.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1.25em;
    line-height: 120%;
    padding: 0 .4em;
    margin-bottom: .5em;
    font-weight: 700;
  }
  input,
  textarea{
    border-radius: .5em;
    border: 1px solid #909090;
    padding: 0 1.6em;
    height: 3.2em;
    width: 100%;
    font-size: 1.25em;
    line-height: 150%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: #909090;
    }
  }
  textarea{
    padding-top: .5em;
    padding-bottom: .5em;
    height: 7em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.125em;
    min-height: 1.25em;
    padding-top: .0625em;
    span{
      font-size: .8125em;
      line-height: 140%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid #DADEDF;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._big{
    label{
      padding-top: 0;
      span{
        font-size: 1em;
        line-height: 140%;
      }
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check2.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}


@media screen and (max-width: 1200px){
  .fg{
    label{
      font-size: 1.125em;
      padding: 0 .44em;
      margin-bottom: .55em;
    }
    input,
    textarea{
      font-size: 1.125em;
      height: 3.11em;
      border-radius: .55em;
      padding-left: 1.22em;
      padding-right: 1.22em;
    }
    textarea{
      height: 7em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .fg{
    label{
      font-size: .975em;
      padding: 0 .57em;
      margin-bottom: .275em;
    }
    input,
    textarea{
      font-size: .875em;
      height: 3.43em;
      border-radius: .57em;
      padding-left: 1em;
      padding-right: 1em;
    }
    textarea{
      height: 7em;
    }
  }
}
