.advantages{
  position: relative;
  padding-top: 12.5em;
  &__inner{
    position: relative;
  }
  &__top{
    text-align: right;
    width: 44em;
    margin-left: auto;
  }
  &__title{
    line-height: 120%;
  }
  &__bottom{
    margin-top: -.5625em;
    padding-left: 2.5em;
    padding-bottom: 2.5em;
    display: grid;
    grid-template-columns: 30.75em calc(100% - 30.75em - 4.31em);
    gap: 4.31em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: -2.78em;
      left: 0;
      width: 100%;
      height: calc(100% + 2.78em);
      background-color: $white;
      z-index: -1;
      clip-path: polygon( 42.403% 5.664%,42.403% 5.664%,42.365% 4.757%,42.253% 3.896%,42.075% 3.093%,41.836% 2.36%,41.542% 1.708%,41.2% 1.149%,40.815% 0.694%,40.393% 0.355%,39.941% 0.143%,39.464% 0.07%,3.078% 0.07%,3.078% 0.07%,2.601% 0.143%,2.149% 0.355%,1.727% 0.694%,1.342% 1.149%,1% 1.708%,0.706% 2.36%,0.467% 3.093%,0.289% 3.896%,0.178% 4.757%,0.139% 5.664%,0.139% 40.909%,0.02% 94.312%,0.02% 94.312%,0.057% 95.222%,0.167% 96.086%,0.344% 96.892%,0.583% 97.628%,0.877% 98.283%,1.22% 98.845%,1.605% 99.302%,2.028% 99.643%,2.481% 99.856%,2.959% 99.93%,49.93% 99.93%,96.996% 99.93%,96.996% 99.93%,97.472% 99.857%,97.925% 99.645%,98.346% 99.306%,98.731% 98.851%,99.074% 98.291%,99.368% 97.64%,99.607% 96.907%,99.785% 96.104%,99.896% 95.243%,99.935% 94.336%,99.935% 53.846%,99.935% 53.846%,99.896% 52.939%,99.785% 52.078%,99.607% 51.275%,99.368% 50.542%,99.074% 49.89%,98.731% 49.331%,98.346% 48.876%,97.925% 48.537%,97.472% 48.325%,96.996% 48.252%,45.342% 48.252%,45.342% 48.252%,44.865% 48.179%,44.413% 47.967%,43.992% 47.627%,43.606% 47.172%,43.264% 46.613%,42.97% 45.961%,42.731% 45.228%,42.553% 44.426%,42.442% 43.565%,42.403% 42.657%,42.403% 5.664% );
    }
    &-col2{
      padding-top: 2.25em;
    }
  }
  &__items{
    @include grid100gap(2.5em);
  }
  &__img{
    position: absolute;
    bottom: 2.38em;
    right: 2.38em;
    height: 18.875em;
    width: calc(100% - 39.9375em);
    img{
      display: block;
      border-radius: 1em;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__bg{
    width: 49.9375em;
    top: -9.34em;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
  }
}

.advantage-item{
  @include grid100gap(.5em);
  &__icon{
    height: 2.5em;
    margin-bottom: .5em;
  }
}

.advantage-item2{
  padding: 2em;
  background-color: $primary;
  color: $white;
  border-radius: 1em;
}


