    }
  }

  .advantage-item2{
    padding: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .advantages{
    padding-top: 9.38em;
    &__top{
      width: 19.6875em;
    }
    &__bottom{
      margin-top: -9em;
      padding: 0;
      grid-template-columns: 100%;
      gap: 1em;
      &:before{
        display: none;
      }
      &-col{
        padding: 1em;
        border-radius: 1.5em;
        background-color: $white;
      }
      &-col2{
        padding: 0;
        order: -1;
        width: calc(100% - 19.6875em);
      }
    }
    &__img{
      height: 15.75em;
      width: 100%;
      position: static;
      margin-top: 1.5em;
    }
    &__bg{
      top: -13em;
    }
  }

  .advantage-item{
    @media screen and (min-width: 577px){
      &__top{
        display: flex;
        align-items: center;
      }
      &__icon{
        width: 2em;
        height: auto;
        margin: 0 .75em 0 0;
      }
    }
  }

  .advantage-item2{
    padding: 1em;
    border-radius: 1.5em;
    .advantage-item__top{
      max-width: 18em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    padding-top: 5.62em;
    &__top{
      width: 100%;
    }
    &__bottom{
      margin-top: 1.5em;
      &-col{
      }
      &-col2{
        width: 100%;
      }
    }
    &__items{
      gap: 1.5em;
    }
    &__img{
      height: 11.3125em;
    }
  }

  .advantage-item{
    &__icon{
      height: 2em;
      margin-bottom: .75em;
    }
  }

  .advantage-item2{
    .advantage-item__top{
      max-width: 100%;
    }
  }
}