.header{
  padding: 1.5em 0;
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 10.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone2{
    display: none;
  }
}

.burger{
  display: none;
}

.phone-link{
  font-weight: 600;
  img{
    width: 1.5em;
    margin-right: .25em;
  }
}

.menu{
  padding: 1.25em 1.5em;
  border-radius: 1.25em;
  background-color: rgba($white,.6);
}

.nav,
.footer-nav{
  color: #444;
  ul{
    display: flex;
  }
  li{
    margin-right: 2.5em;
    &:last-child{
      margin: 0;
    }
  }
  a{
    font-size: 1em;
    line-height: 150%;
  }
  ._active{
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      justify-content: flex-end;
    }
    &__logo{
      margin-right: auto;
      position: relative;
      z-index: 11;
    }
    &__phone2{
      display: flex;
      margin-top: 5em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    transform: unset;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 4.375em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    margin-left: 1.5em;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 80%;
    }
  }

  .nav,
  .footer-nav{
    ul{
      display: block;
    }
    li{
      margin: 0 0 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 1em 0;
    &__phone{
      display: none;
    }
  }
}
