@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    background-image: url(../img/footer-bg.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: left bottom;
    overflow: hidden;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: calc(100vw * (16 / 1440));
    background-color: #F5F5F5;
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 2.5em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 1200px){
    .m-page{
        font-size: calc(100vw * (16 / 1024));
    }

    .wrapper{
        background-image: url(../img/footer-bg2.svg);
    }

    .container{
        padding: 0 2em;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }

    .wrapper{
        background-image: url(../img/footer-bg3.svg);
    }

    .container{
        padding: 0 1.5em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 320));
    }

    .wrapper{
        background-image: url(../img/footer-bg4.svg);
    }

    .container{
        padding: 0 1em;
    }
}


