@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 4em;
    border-radius: .625em;
    font-weight: 700;
    span{
        font-size: 1.25em;
    }
    &:focus {
        box-shadow: none;
    }
}

.m-btn-black{
    color: $white;
    background-color: $black;
    &:hover{
        color: $white;
        background-color: $primary;
    }
}

.m-btn-red{
    color: $white;
    background-color: $primary;
    &:hover{
        color: $white;
        background-color: darken($primary,10%);
    }
}


@media screen and (max-width: 1200px){
    .m-btn{
        height: 3.5em;
    }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
    .m-btn{
        height: 3em;
        span{
            font-size: 1em;
        }
    }
}
