.contacts{
  &__grid{
    display: grid;
    grid-template-columns: 25.5em calc(100% - 28.5em);
    gap: 3em;
    margin-top: 3.5em;
  }
  &__items{
    @include grid100gap(1.5em);
  }
}

.contacts-map{
  border-radius: 2.5em;
  height: 30.625em;
  overflow: hidden;
  position: relative;
  &__info{
    position: absolute;
    top: 2em;
    left: 2em;
    width: 26.375em;
    padding: 1.5em;
    border-radius: .5em;
    background-color: $white;
    .text32{
      letter-spacing: -0.02em;
    }
  }
}


@media screen and (max-width: 1200px){
  .contacts{
    &__grid{
      grid-template-columns: 19em calc(100% - 20.5em);
      gap: 1.5em;
      margin-top: 3em;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2.5em;
    }
    &__items{
      gap: 1em;
      @media screen and (min-width: 577px){
        grid-template-columns: 18.3125em 10.25em auto;
        justify-content: space-between;
      }
    }
  }

  .contacts-map{
    border-radius: 2em;
    height: 27.5em;
    &__info{
      top: 1em;
      left: 1em;
      width: 26.375em;
      padding: 1em;
      border-radius: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    padding-bottom: 2em;
    &__grid{
      margin-top: 1.5em;
    }
  }

  .contacts-map{
    height: 22.375em;
    &__info{
      width: 13.75em;
      padding: .5em 1em;
    }
  }
}