.breadcrumbs {
    padding: 2em 0 3.5em;
    li{
        display: inline;
        color: #000;
        position: relative;
        &:last-child {
            pointer-events: none;
        }
        &:not(:first-child) {
            margin-left: .75em;
            &:before {
                content: '/';
                position: absolute;
                top: 0;
                right: 100%;
                margin-right: .25em;
                font-size: 1.125em;
                height: 133%;
            }
        }
    }
    a{
        font-size: 1.125em;
        height: 133%;
        &:hover{
            color: $black;
        }
    }
}


@media screen and (max-width: 1200px){
    .breadcrumbs{
        padding: 1.5em 0 3em;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1em 0 2.5em;
        li:not(:first-child):before,
        a{
            font-size: 1em;
            line-height: 150%;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        padding: .5em 0 1.5em;
        li:not(:first-child):before,
        a{
            font-size: .75em;
            line-height: 200%;
        }
        li:not(:first-child){
            margin-left: .5em;
        }
    }
}