.catalog{
  position: relative;
  z-index: 2;
  &__grid{
    display: grid;
    margin-top: 3.5em;
    grid-template-columns: 20.125em calc(100% - 21.625em);
    gap: 2em 1.5em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
}

.catalog-search{
  margin-left: .625em;
  position: relative;
  &__field{
    position: absolute;
    width: 25em;
    top: 100%;
    right: 0;
    margin-top: .5em;
    height: 3em;
    input{
      height: 100%;
      background-color: $white;
      border-color: $white;
    }
  }
  &__btn{
    width: 3em;
    height: 3em;
    border-radius: .5em;
    background-color: $white;
    @include center;
    img{
      width: 1.5em;
    }
  }
  @media screen and (min-width: 577px){
    &__field{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &._open{
      .catalog-search__field{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.catalog-filter{
  @include grid100gap(3em);
}

.filter-item{
  position: relative;
  .fg{
    label{
      padding: 0;
    }
    input{
      cursor: pointer;
      padding-left: .7em;
      background-image: url(../img/select-arrow.svg);
      background-repeat: no-repeat;
      background-size: 1.2em auto;
      background-position: right .4em center;
    }
    @media screen and (min-width: 1201px){
      label{
        font-size: 1.25em;
      }
      input{
        font-size: 1.25em;
        height: 3.2em;
        border-radius: .5em;
      }
    }
  }
  &__dropdown{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .5em;
    width: 100%;
    border-radius: 0.625em;
    border: 1px solid #909090;
    padding: 1em;
    background-color: $white;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    ul{
      @include grid100gap(1.5em);
    }
    a{
      font-size: 1.125em;
      line-height: 133%;
    }
    ._selected{
      color: $primary;
    }
  }
  &._open{
    z-index: 3;
    .filter-item__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._type2{
    @media screen and (min-width: 993px){
      .filter-item__show{
        label{
          margin: 0;
        }
        input{
          display: none;
        }
      }
      .filter-item__dropdown{
        margin-top: 1.5em;
        position: static;
        opacity: 1;
        pointer-events: unset;
        padding: 0;
        background-color: transparent;
        border: none;
      }
    }
  }
}

