    }
  }
  &__nav{
    margin-top: auto;
    padding-top: .875em;
  }
  &__link{
    width: 100%;
    height: 3.1875em;
    border-radius: 6.25em;
    font-weight: 500;
    span{
      font-size: 1.125em;
    }
    img{
      width: 1.5em;
      margin-left: .625em;
    }
  }
}


@media screen and (max-width: 1200px){
  .feedback2{
    .feedback__bg,
    .feedback__bg2{
      display: none;
    }
  }

  .catalog{
    &__grid{
      margin-top: 3em;
      grid-template-columns: 19em calc(100% - 20.5em);
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
    }
  }

  .catalog-card{
    min-height: 28em;
    padding: 1.25em;
    &__img{
      height: 14.625em;
      padding: 1.25em;
      margin-bottom: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: 100%;
    }
  }

  .catalog-filter{
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1em 1.25em;
  }

  .catalog-card{
    min-height: 27.5em;
    padding: 1em;
    border-radius: 2em;
    &__img{
      margin-bottom: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__top{
      display: block;
      text-align: center;
    }
    &__grid{
      margin-top: 1.5em;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .catalog-search{
    margin: .625em 0 0;
    &__btn{
      position: absolute;
      top: 0;
      left: 0;
      width: 3em;
      height: 2.5em;
    }
    &__field{
      width: 100%;
      height: 2.5em;
      position: static;
      input{
        padding-left: 3.43em;
      }
    }
  }

  .catalog-filter{
    grid-template-columns: 100%;
  }

  .catalog-card{
    min-height: unset;
    border-radius: 1.5em;
    &__img{
      margin-bottom: .5em;
      height: 10.9em;
    }
    &__nav{
      padding-top: 2.5em;
    }
    &__link{
      height: 2.5em;
      span{
        font-size: 1em;
      }
    }
  }
}