.categories{
  overflow: hidden;
  border-radius: 1.5em;
  background-color: $white;
  margin-top: -3.75em;
  z-index: 2;
  position: relative;
}

.categories-slider{
  padding: 0 3.75em 0 5.5em;
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      position: relative;
      width: calc(100% / 11);
      height: auto;
      padding: .5em .125em;
      &:after,
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 1px;
        height: calc(100% - 3.875em);
        background-color: #e1e1e1;
      }
      &:before{
        left: 0;
        opacity: 0;
      }
      &:after{
        right: 0;
      }
      &-active{
        &:before{
          opacity: 1;
        }
      }
    }
    &-button{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      width: 5.5em;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 1.5em;
      &-prev{
        left: 0;
        justify-content: flex-end;
      }
      &-next{
        right: 0;
      }
      img{
        width: 1.5em;
      }
    }
  }
}

.category-card{
  height: 100%;
  text-align: center;
  padding: .8125em .25em;
  border-radius: 1em;
  transition: all .3s;
  display: block;
  &:hover{
    background: rgba(204, 7, 30, 0.1);
    color: $primary;
  }
  &__img{
    height: 4.5em;
    @include center;
    margin-bottom: .5em;
    img{
      height: 100%;
    }
  }
  &__title{
    font-weight: 500;
    font-size: .875em;
    line-height: 129%;
    font-family: $font2;
  }
}


@media screen and (max-width: 1200px){
  .categories{
    margin-top: -1.5em;
  }

  .categories-slider{
    padding: 0 6.25em;
    .swiper{
      &-slide{
        width: calc(100% / 7);
      }
      &-button{
        width: 6.25em;
        justify-content: center;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .categories-slider{
    padding: 0 1.25em;
    .swiper{
      &-slide{
        width: calc(100% / 6);
        &:before{
          display: none;
        }
      }
      &-button{
        width: 6.0625em;
        &-prev{
          justify-content: unset;
          background: linear-gradient(270deg, #F9F9F9 62.5%, rgba(249, 249, 249, 0.00) 100%);
        }
        &-next{
          justify-content: flex-end;
          background: linear-gradient(270deg, #F9F9F9 43.18%, rgba(249, 249, 249, 0.00) 100%);
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .categories-slider{
    padding: 0;
    .swiper{
      &-slide{
        width: 6em;
        padding-top: 0;
        padding-bottom: 0;
        &:after{
          display: none;
        }
      }
      &-button{
        width: 3.1875em;
        justify-content: center;
        padding: 0;
      }
    }
  }

  .category-card{
    padding-top: 1em;
    padding-bottom: 1em;
  }
}
