.footer{
  padding: 12.5em 0 6.36em;
  &__inner{
    width: 77em;
    max-width: 100%;
    margin: 0 auto;
    border-radius: 2.5em;
    border: 1px solid $white;
    background: rgba(255, 255, 255, 0.60);
    padding: 4.5em;
  }
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    &-col{
      max-width: 22em;
    }
    &-col2{
      padding-bottom: 2.94em;
    }
  }
  &__logo{
    display: block;
    width: 10.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__time{
    margin-top: 2em;
    color: #444;
  }
  &__bottom{
    margin-top: 2.5em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
}



.footer-contacts{
  &__btn{
    padding: 0 3.75em;
    margin-left: 1.5em;
  }
}


@media screen and (max-width: 1200px){
  .footer{
    padding: 10em 0 5.125em;
    &__inner{
      padding: 3em;
    }
    &__top{
      &-col2{
        padding-bottom: 2.5em;
      }
    }
    &__bottom{
      margin-top: 3em;
    }
  }

  .footer-nav{
    li{
      margin-right: 2em;
    }
  }

  .footer-contacts{
    &__btn{
      height: 3.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 9.375em 0 7em;
    &__top{
      align-items: unset;
      &-col2{
        padding: 0;
      }
    }
    &__bottom{
      margin-top: 3.75em;
    }
  }

  .footer-nav{
    li{
      text-align: right;
    }
  }

  .footer-contacts{
    flex-direction: column;
    align-items: flex-end;
    &__btn{
      margin: 1em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 6.625em 0 2.5em;
    text-align: center;
    &__inner{
      padding: 1.5em;
      border-radius: 1.5em;
    }
    &__top{
      flex-direction: column;
      align-items: center;
      &-col2{
        margin-top: 2em;
      }
    }
    &__logo{
      margin: 0 auto;
    }
    &__time{
      margin-top: 1em;
      padding: 0 2em;
    }
    &__bottom{
      margin-top: 2em;
      flex-direction: column;
      align-items: center;
      &-col2{
        order: -1;
        margin-bottom: 2em;
      }
    }
  }

  .footer-nav{
    li{
      margin-bottom: .5em;
      text-align: center;
    }
    a{
      font-size: .875em;
    }
  }

  .footer-contacts{
    align-items: center;
    &__btn{
      width: 100%;
    }
  }
}
