.feedback{
  padding-top: 12.5em;
  position: relative;
  &__bg{
    position: absolute;
    bottom: 5em;
    left: 0;
    mix-blend-mode: color-burn;
    height: 32.8125em;
  }
  &__bg2{
    position: absolute;
    top: 1.25em;
    right: .4em;
    mix-blend-mode: color-burn;
    width: 18.4375em;
  }
  &__inner{
    width: 100%;
    max-width: 56em;
    margin: 0 auto;
  }
}

.feedback-form{
  width: 100%;
  max-width: 43.5em;
  margin: 2.5em auto 0;
  &__fields{
    @include grid100gap(1.25em);
  }
  &__btn{
    width: 100%;
    margin-top: 2.5em;
  }
}

.index-page{
  .header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
  }
  .wrapper{
    overflow: hidden;
  }
}

.main-slider{
  overflow: hidden;
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      top: 50%;
      transform: translateY(-50%);
      width: 2.5em;
      height: 2.5em;
      border-radius: 50%;
      @include center;
      background-color: rgba($black, .6);
      &:hover{
        background-color: $black;
      }
      img{
        width: 100%;
      }
      &-prev{
        left: 2.5em;
      }
      &-next{
        right: 2.5em;
      }
    }
    &-pagination{
      z-index: 1;
      bottom: 4.75em;
      @include center;
      &-bullet{
        margin: 0 .125em !important;
        width: 4em;
        height: .25em;
        border-radius: .25em;
        background-color: rgba($black, .5);
        &-active{
          background-color: $black;
        }
      }
    }
  }
}

.main-slide{
  position: relative;
  min-height: 41em;
  padding: 11.5em 0 9.75em;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__img{
    z-index: -1;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__text{
    @include grid100gap(1em);
    width: 100%;
    max-width: 41.75em;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 140%;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
  }
  &__link{
    height: 4.375em;
  }
}


@media screen and (max-width: 1200px){
  .feedback{
    padding-top: 10em;
    &__bg{
      bottom: -6.12em;
      height: 25.25em;
    }
    &__bg2{
      top: -5em;
      right: -4.1em;
      width: 12.3125em;
    }
    &__inner{
      max-width: 34.875em;
    }
  }

  .feedback-form{
    max-width: 29.25em;
  }

  .main-slider{
    .swiper{
      &-button{
        &-prev{
          left: 2em;
        }
        &-next{
          right: 2em;
        }
      }
      &-pagination{
        bottom: 2.75em;
      }
    }
  }

  .main-slide{
    min-height: 37.75em;
    padding: 11.5em 0 7.5em;
    &__link{
      height: 3.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .feedback{
    padding-top: 10.375em;
    &__bg{
      bottom: -12.31em;
    }
    &__bg2{
      top: -3em;
      right: -3.5em;
      width: 11.6875em;
    }
  }

  .main-slider{
    .swiper{
      &-button{
        &-prev{
          left: 1.5em;
        }
        &-next{
          right: 1.5em;
        }
      }
      &-pagination{
        bottom: 2.5em;
      }
    }
  }

  .main-slide{
    min-height: 37.75em;
    padding: 9.875em 0 5.375em;
    &__text{
      max-width: 33.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .feedback{
    padding-top: 5.625em;
    &__bg{
      bottom: unset;
      height: 12.6875em;
      top: -4.5em;
    }
    &__bg2{
      display: none;
    }
    &__hint{
      margin-top: .5em;
      padding: 0 2em;
    }
  }

  .feedback-form{
    margin-top: 1em;
    &__fields{
      gap: .5em;
    }
    &__btn{
      margin-top: 1em;
    }
  }

  .main-slider{
    .swiper{
      &-button,
      &-pagination{
        display: none;
      }
    }
  }

  .main-slide{
    min-height: 32.5em;
    padding: 9.3em 0 7.5em;
    &__text{
      max-width: 17.5em;
    }
    &__desc{
      font-size: 1em;
    }
    &__link{
      height: 3em;
    }
  }
}